webpackJsonp([87849259185803],{

/***/ 318:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"markdown-features\"><a href=\"#markdown-features\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Markdown features</h1>\n<p>Gatsby-docs-kit support some extra features when writing pages in markdown.</p>\n<h2 id=\"code-and-syntax-highlighting\"><a href=\"#code-and-syntax-highlighting\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Code and Syntax Highlighting</h2>\n<p>Syntax highlighting is enabled by default on all code blocks. By default we are using PrismJs. The language should be detected automatically, but you can sometimes get better results by specifying the language. You can do so using an info string, following the three opening backticks. Blocks of code can be also indented with four spaces. We recommend only using the fenced code blocks -- they're easier and only they support syntax highlighting.</p>\n<h3 id=\"for-example\"><a href=\"#for-example\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>For example</h3>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">```javascript\nconst s = &#39;Test&#39;;\nalert(s);\n```\n\n# In the following code snippet, lines 1 and 4 through 6 will get the line highlighting\n```javascript{1,4-6}\n// In your gatsby-config.js\nplugins: [\n  {\n    resolve: `gatsby-transformer-remark`,\n    options: {\n      plugins: [\n        `gatsby-remark-prismjs`,\n      ]\n    }\n  }\n]\n```</code></pre>\n      </div>\n<div class=\"gatsby-highlight\" data-language=\"javascript\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">const</span> s <span class=\"token operator\">=</span> <span class=\"token string\">'JavaScript syntax highlighting'</span><span class=\"token punctuation\">;</span>\n<span class=\"token function\">alert</span><span class=\"token punctuation\">(</span>s<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\" data-language=\"javascript\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"gatsby-highlight-code-line\"><span class=\"token comment\">// In your gatsby-config.js</span>\n</span>plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token punctuation\">{</span>\n<span class=\"gatsby-highlight-code-line\">    resolve<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`gatsby-transformer-remark`</span></span><span class=\"token punctuation\">,</span>\n</span><span class=\"gatsby-highlight-code-line\">    options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n</span><span class=\"gatsby-highlight-code-line\">      plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n</span>        <span class=\"token template-string\"><span class=\"token string\">`gatsby-remark-prismjs`</span></span><span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">]</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">]</span></code></pre>\n      </div>\n<p>To get more info about the line page parsing, see <a href=\"https://www.npmjs.com/package/parse-numeric-range\">parse-numeric-range</a>.</p>\n<h2 id=\"preview-react-components\"><a href=\"#preview-react-components\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Preview React Components</h2>\n<p>Gatsby-Docs-Kit uses <a href=\"https://react-live.kitten.sh/\">React live</a> to provide support for rendering and live editing React components. To enable it, start code block like so:</p>\n<div class=\"gatsby-highlight\" data-language=\"markdown\">\n      <pre class=\"language-markdown\"><code class=\"language-markdown\">```jsx render\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Carousel</span>\n    <span class=\"token attr-name\">autoPlay</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span>{2000}</span>\n    <span class=\"token attr-name\">infinite</span>\n    <span class=\"token attr-name\">arrows</span>\n  <span class=\"token punctuation\">></span></span>\n<span class=\"token code keyword\">    &lt;img src=\"https://1fichier.com/?fili3p7aoh\" /></span>\n<span class=\"token code keyword\">    &lt;img src=\"https://1fichier.com/?bbk02e1jci\" /></span>\n<span class=\"token code keyword\">    &lt;img src=\"https://1fichier.com/?jltyz4xk0w\" /></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Carousel</span><span class=\"token punctuation\">></span></span>\n'``</code></pre>\n      </div>\n<p>this will be rendered as follows:</p>\n<br />\n\n  <div>\n    <div class=\"gatsby-render-code\" ></div>\n    <div class=\"gatsby-render-source-code\" style=\"display:none;\">\n    &lt;Carousel\n  autoPlay={2000}\n  infinite\n  arrows\n&gt;\n  &lt;img src=&quot;https://1fichier.com/?fili3p7aoh&quot; /&gt;\n  &lt;img src=&quot;https://1fichier.com/?bbk02e1jci&quot; /&gt;\n  &lt;img src=&quot;https://1fichier.com/?jltyz4xk0w&quot; /&gt;\n&lt;/Carousel&gt;\n    </div>\n  </div>\n<blockquote>\n<p>You must specify all variables used within that code block. To do so, export them from <code class=\"language-text\">src/globalReferences.js</code>.</p>\n</blockquote>\n<p>To make previous example work, <code class=\"language-text\">src/globalReferences.js</code> should looks like:</p>\n<div class=\"gatsby-highlight\" data-language=\"js\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">import</span> Carousel <span class=\"token keyword\">from</span> <span class=\"token string\">'@brainhubeu/react-carousel'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">import</span> <span class=\"token string\">'@brainhubeu/react-carousel/lib/style.css'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token punctuation\">{</span> Carousel <span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>To see all available features for rendering React components, see documentation of <a href=\"https://react-live.kitten.sh/\">React live</a>.</p>\n<h2 id=\"linking-other-documents\"><a href=\"#linking-other-documents\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Linking other documents</h2>\n<p>You can use relative urls to other markdown file which will automatically get converted to the corresponding html links when they get rendered.</p>\n<h3 id=\"for-example-1\"><a href=\"#for-example-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>For example:</h3>\n<div class=\"gatsby-highlight\" data-language=\"markdown\">\n      <pre class=\"language-markdown\"><code class=\"language-markdown\"><span class=\"token url\">[Imagine link to other markdown](other-document.md)</span></code></pre>\n      </div>\n<p>If you created <code class=\"language-text\">other-document.md</code> and you added it navigation config, then it will be converted into link pointing to corresponding page.</p>\n<h2 id=\"linking-images-and-other-assets\"><a href=\"#linking-images-and-other-assets\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Linking images and other assets</h2>\n<p>Static assets (like images, pdf's) can be linked using relative urls. All static asset will be copied to <code class=\"language-text\">public</code> directory with static assets.</p>\n<p>For example following syntax:</p>\n<div class=\"gatsby-highlight\" data-language=\"markdown\">\n      <pre class=\"language-markdown\"><code class=\"language-markdown\"><span class=\"token url\">![Test image](../assets/example.jpg)</span></code></pre>\n      </div>\n<p>will result with:</p>\n<p><img src=\"../assets/test.jpg\" alt=\"Test image\"></p>\n<h2 id=\"inline-html\"><a href=\"#inline-html\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Inline HTML</h2>\n<p>You can also use raw HTML in your Markdown, and it'll mostly work pretty well.</p>\n<div class=\"gatsby-highlight\" data-language=\"html\">\n      <pre class=\"language-html\"><code class=\"language-html\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token style-attr language-css\"><span class=\"token attr-name\"> <span class=\"token attr-name\">style</span></span><span class=\"token punctuation\">=\"</span><span class=\"token attr-value\"><span class=\"token property\">font-style</span><span class=\"token punctuation\">:</span> italic<span class=\"token punctuation\">;</span> <span class=\"token property\">font-size</span><span class=\"token punctuation\">:</span> 15px</span><span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span> Custom paragraph <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<p style=\"font-style: italic; font-size: 15px\"> Custom paragraph </p>"}},"pathContext":{"relativePath":"GDK>../docs/guides/markdown-features.md"}}

/***/ })

});


